.prices {

	&__inner {
		padding: px-to-rem(75, 18) 0 px-to-rem(95, 18);
		border-top: 1px solid rgba($accent, .3);
		border-bottom: 1px solid rgba($accent, .3);

		@include media (xl) {
			padding: px-to-rem(90, 16) 0;
		}

		@include media (lg) {
			padding: px-to-rem(60, 16) 0;
		}
	}

	&__title-group {
		margin-bottom: px-to-rem(110, 18);

		@include media (xl) {
			margin-bottom: px-to-rem(90, 16);
		}

		@include media (md) {
			br {
				display: none;
			}
		}
	}

	&__list {
		margin: 0;
		display: flex;
		align-items: flex-start;
		justify-content: center;

		@include media (xl) {
			flex-wrap: wrap;
		}
	}

	&__wrap-item {
		padding: 0 px-to-rem(25, 18) 0;
		flex-shrink: 0;
		width: calc(100% / 3 + 60px);
		transition: .8s all ease;

		@include media (xxl) {
			width: calc(100% / 3);
			padding: 0 px-to-rem(20, 18) 0;
		}

		@include media (xl) {
			width: calc(50% - 80px);
			margin-bottom: px-to-rem(80, 16);

			&:last-child {
				margin-bottom: 0;
			}
		}

		@include media (lg) {
			width: 50%;
		}

		@include media (md) {
			width: 100%;
			max-width: 400px;

			&:last-child {
				margin-bottom: px-to-rem(40, 14);
			}
		}

		@include media (sm) {
			padding: 0;
			margin-bottom: px-to-rem(90, 16);
		}

		&_active {
			.prices__item {
				width: 100%;
				margin-bottom: px-to-rem(40, 18);

				@include media (xl) {
					&:last-child {
						margin-bottom: 0;
					}
				}

				@include media (md) {
					margin-bottom: px-to-rem(40, 14);
				}
 
				@include media (sm) {
					margin-bottom: px-to-rem(60, 16);
				}

				&:before,
				&:after {
					width: 100%;
					border-radius: 0;
					top: px-to-rem(-25, 18);
					height: calc(100% - 45px);

					@include media (sm) {
						top: 0;
					}
				}

				&:before {
					opacity: 0;
				}

				&:after {
					opacity: 1;
				}

				&-top {
					width: 100%;
				}

				&-wrap-img {
					max-width: 208px;
					margin-top: -40px;

					@include media (xxl) {
						max-width: 160px;
						margin-top: -25px;
					}

					@include media (xl) {
						max-width: 195px;
					}
				}

				&-name {
					transform: scale(1.22);

					@include media (xxl) {
						transform: scale(1.17);
					}

					@include media (xl) {
						transform: scale(1.22);
					}

					&:after {
						background-color: $accent;
					}
				}

				&-wrap-text {
					opacity: 1;
				}

				&-btn {
					&-control {
						margin: 0 auto;
					}
				}
			}
		}
	}

	&__item {
		position: relative;

		&:before,
		&:after {
			content: '';
			position: absolute;
			top: 0;
			left: 50%;
			transform: translate(-50%, 0);
			width: 100%;
			height: 100%;
			transition: .6s all ease;
			border-radius: 30px;
			z-index: -1;
			width: 91.25%;
		}

		&:before {
			background: $gradient-purple-light;
		}

		&:after {
			background: $gradient-purple;
			opacity: 0;
		}

		&-top {
			display: flex;
			justify-content: space-between;
			width: 91.25%;
			margin: 0 auto;
			transition: .8s all ease;

			@include media (sm) {
				display: block;
			}
		}

		&-wrap-img {
			position: relative;
			width: 100%;
			max-width: 148px;
			margin-left: -30px;
			transition: .8s all ease;

			@include media (xxl) {
				max-width: 115px;
				margin-left: -20px;
			}

			@include media (xl) {
				max-width: 148px;
				margin-left: -30px;
			}

			@include media (sm) {
				margin: 0 auto 30px;
			}

			&:before {
				@include stretchHeight(100%);
			}
		}

		&-img {
			width: 100%;
			height: auto;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			transition: .8s all ease;

			@include media (xxl) {
				top: 40%;
			}
		}

		&-wrap-info {
			padding: 22px 25px 0 0;
			display: inline-flex;
			flex-direction: column;
			transition: .8s all ease;

			@include media (sm) {
				align-items: center;
				text-align: center;
				padding: 0;
				width: 100%;
			}
		}

		&-name {
			@include C-ExtraBold;
			padding-bottom: px-to-rem(10, 18);
			margin-bottom: px-to-rem(40, 18);
			position: relative;
			display: inline-block;
			transition: .6s all ease;
			transform-origin: 0 50%;

			@include media (xxl) {
				padding-bottom: px-to-rem(8, 18);
				margin-bottom: px-to-rem(30, 18);
			}

			@include media (md) {
				padding-bottom: px-to-rem(8, 14);
			}

			@include media (sm) {
				transform-origin: 50% 50%;
			}

			&:after {
				content: '';
				position: absolute;
				top: 100%;
				left: 0;
				width: 50px;
				height: 1px;
				transition: inherit;
				background-color: rgba($accent, .32);

				@include media (sm) {
					left: 50%;
					transform: translate(-50%, 0);
				}
			}
		}

		&-price {
			@include C-Bold;
			display: inline-block;

			&-text,
			&-number {
				display: inline-block;
			}

			&-text {
				font-size: px-to-rem(36, 18);
				margin-right: 10px;

				@include media (xxl) {
					font-size: px-to-rem(30, 18);
				}

				@include media (md) {
					font-size: px-to-rem(32, 14);
				}
			}

			&-number {
				font-size: px-to-rem(48, 18);

				@include media (xxl) {
					font-size: px-to-rem(42, 18);
				}

				@include media (md) {
					font-size: px-to-rem(44, 14);
				}

				@include media (sm) {
					font-size: px-to-rem(38, 16);
				}
			}
		}

		&_bottom {
		
		}

		&-wrap-text {
			display: none;
			margin-top: px-to-rem(35, 18);
			padding: 0 15px;
			text-align: center;
			opacity: 0;
			transition: .4s opacity ease;
	
			li {
				font-size: 16px;
				line-height: 32px;

				@include media (sm) {
					font-size: 14px;
					line-height: 26px;
				}
			}
		}

		&-btn {
			margin: 0 auto;

			&-hidden {
				margin-top: px-to-rem(40, 18);
			}

			&-control {
				margin: px-to-rem(52.5, 18) auto 0; // Half button
				transform: translate(0, px-to-rem(27.5, 18)); // Half button
			}
		}
	}

	&__wrap-bottom-btn {
		text-align: center;
		margin-top: px-to-rem(70, 18);
	}

	&__bottom-btn {
		color: $link;
		display: inline-block;
		margin-left: 3px;
		@include C-Bold;
		font-size: inherit;
		transition: .4s all ease;

		@include media (sm) {
			display: block;
			margin: 0 auto;
		}

		&:hover {
			color: $accent;
		}
	}

}
