.panel {
	display: flex;
	align-items: center;
	position: relative;

	@include media (lg) {
		justify-content: space-between;
	}

	&__logo {
		display: flex;
		align-items: center;
		width: 150px;
		max-height: 100%;
		flex-shrink: 0;
		margin-right: px-to-rem(120, 18);

		@include media (lg) {
			width: 120px;
			order: 1;
			margin: 0;
		}

		@include media (sm) {
			width: 100px;
		}

		&-img {
			width: 100%;
			height: auto;
			max-height: 100%;
			max-width: 100%;
			display: block;
		}
	}

	&__nav {
		position: relative;
		top: 10px;

		@include media (xl) {
			top: 5px;
		}

		@include media (lg) {
			display: none;
		}

		&-list {
			display: flex;
			align-items: center;
		}

		&-item {
			margin-right: px-to-rem(36, 18);

			&:last-child {
				margin-right: 0;
			}
		}

		&-link {
			display: block;

			&:hover {
				color: $link;
			}
		}
	}

	&__phone {
		margin-left: auto;
		text-align: right;

		@include media (lg) {
			order: 3;
		}

		&-text, &-number {
			display: block;
			line-height: 24px;
		}

		&-text {
			margin-bottom: 2px;
		}

		&-number {
			@include C-ExtraBold;

			&:hover {
				color: $link;
			}
		}
	}

	&__menu-btn {
		width: 34px;
		height: 24px;
		align-items: center;
		justify-content: center;
		flex-direction: column;
		display: none;
		cursor: pointer;
		position: relative;

		&_mobile {
			position: absolute;
			top: px-to-rem(35, 18);
			right: 15px;

			@include media (sm) {
				top: px-to-rem(25, 16);
			}

			@include media (xs) {
				top: px-to-rem(25, 13);
			}
		}

		@include media (lg) {
			display: flex;
			order: 2;
		}

		&-line {
			width: 100%;
			height: 3px;
			background-color: $accent;
			transform-origin: 50% 50%;
			position: absolute;
			left: 50%;
			transform: translate(-50%, -50%);
			transition: .4s all ease;

			&:nth-child(1) {
				top: 0;
			}
			
			&:nth-child(2) {
				top: calc(50% - 1px);
			}

			&:nth-child(3) {
				bottom: 0;
			}
		}

		&_active {
			.panel__menu-btn-line {
				&:nth-child(1) {
					top: calc(50% - 2px);
					transform: translate(-50%, 0) rotate(-45deg)
				}

				&:nth-child(3) {
					bottom: calc(50% - 1px);
					transform: translate(-50%, 0) rotate(45deg)
				}

				&:nth-child(2) {
					opacity: 0;
				}
			}
		}
	}
	
}
