.btn {
	font-size: 18px;
	background-color: $accent;
	text-align: center;
	line-height: px-to-em(55, 18);
	max-width: 265px;
	width: 100%;
	display: block;
	border-radius: 30px;
	color: #fff;
	cursor: pointer;
	border: 1px solid $accent;
	@include C-ExtraBold;
	transition: .4s all ease;

	@include media (md) {
		font-size: 16px;
	}

	@include media (sm) {
		line-height: px-to-em(48, 16);
	}

	&:hover {
		background-color: $btn-hover;
		border-color: $btn-hover;
	}

	&_bold {
		@include C-Bold;
	}

	&_transparent {
		background-color: transparent;
		display: flex;
		align-items: center;
		justify-content: center;
		text-align: center;

		.btn__text {
			position: relative;
			margin: 0 auto;

			&:after {
				content: '';
				position: absolute;
				top: 50%;
				right: -20px;
				transform: translate(0, -50%);
				transition: .4s all ease;
				width: 9px;
				height: 6px;
				background: url("../img/icon-arrow-down-white.svg") center/contain no-repeat;
			}
		}

		&_active {
			.btn__text {
				&:after {
					transform: translate(0, -50%) rotate(180deg);
				}
			}
		}
	}
}



