@mixin font-face($font-family, $file-path, $weight: normal, $style: normal, $font-display: swap, $asset-pipeline: false ) {
	@font-face {
		font-family: $font-family;
		font-weight: $weight;
		font-style: $style;
		font-display: $font-display;
		src: url("#{$file-path}.woff") format("woff"), url("#{$file-path}.woff2") format("woff2");
	}
}