.faq {
	padding: px-to-rem(150, 18) 0 px-to-rem(180, 18);

	@include media (lg) {
		padding-bottom: px-to-rem(130, 16);
	}

	@include media (md) {
		padding-bottom: px-to-rem(110, 14);
	}

	@include media (sm) {
		padding: px-to-rem(130, 16) 0 px-to-rem(80, 16);
	}

	&__title {
		margin-bottom: px-to-rem(90, 18);

		@include media (md) {
			margin-bottom: px-to-rem(50, 14);
		}

		@include media (xs) {
			margin-bottom: px-to-rem(40, 13);
		}
	}

	&__item {
		&-top {
			display: flex;
			align-items: center;
			justify-content: space-between;
			cursor: pointer;
			padding: px-to-rem(26, 18) 0;
			transition: .4s all ease;
			border-bottom: 1px solid rgba($accent, .32);

			@include media (md) {
				padding: px-to-rem(16, 14) 0;
			}

			&:hover:not(.faq__item-top_active) {
				.faq__item {
					&-title {
						color: $dodger-blue;

						@include media (lg) {
							color: #fff;
						}
					}

					&-icon {
						path {
							stroke: $dodger-blue;

							@include media (lg) {
								stroke: #fff;
							}
						}
					}
				}
			}

			&_active {
				border-color: transparent;

				.faq__item {
					&-title {
						color: $accent;
						opacity: 1;
					}

					&-icon {
						transform: rotate(180deg);
						opacity: 1;

						path {
							stroke: $accent;
						}
					}
				}
			}
		}

		&-title {
			transition: .3s all ease;
			font-size: px-to-rem(20, 18);
			line-height: px-to-rem(26, 18);
			opacity: .7;
			max-width: 90%;

			@include media (xl) {
				br {
				display: none;
				}
			}

			@include media (md) {
				font-size: px-to-rem(18, 14);
				line-height: px-to-rem(26, 14);	
			}

			@include media (sm) {
				font-size: px-to-rem(16, 16);
				line-height: px-to-rem(24, 16);	
			}
		}

		&-icon {
			width: 30px;
			height: 30px;
			position: relative;
			transition: .3s all ease;

			svg {
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				width: 15px;
				height: auto;
			}

			path {
				transition: .4s all ease;
			}
		}

		&-text {
			display: none;
			padding: px-to-rem(48, 18) px-to-rem(15, 18) px-to-rem(48, 18) px-to-rem(40, 18);

			@include media (xl) {
				br {
					display: none;
				}
			}

			@include media (md) {
				padding: px-to-rem(35, 14) px-to-rem(20, 14);
			}

			p {
				margin-bottom: px-to-rem(20, 18);

				&:last-child {
					margin-bottom: 0;
				}
			}
		}
	}

}
