.feedback {
	position: relative;

	&__inner {
		padding: px-to-rem(200, 18) 0 px-to-rem(130, 18);
		position: relative;

		@include media (lg) {
			padding: px-to-rem(150, 16) 0 px-to-rem(110, 16);
		}

		@include media (md) {
			padding: px-to-rem(80, 14) 0 px-to-rem(80, 14);
		}
	}

	&__bg {
		position: absolute;
		left: 50%;
		top: 0;
		transform: translate(-50%, 0);
		width: 100%;
		max-width: 690px;
		z-index: -1;

		&:before {
			@include stretchHeight(100%);
		}

		&-text {
			position: absolute;
			top: -20px;
			right: 89%;
			padding: px-to-rem(12, 18) px-to-rem(28, 18);
			color: $honey-flower;
			background-color: #fff;
			border-radius: 30px;
			z-index: 1;
			@include C-Bold;
			white-space: nowrap;

			@include media (xl) {
				right: 77%;
			}

			@include media (lg) {
				right: 50%;
				transform: translate(50%, -50%);
				top: 0;
			}

			&:after {
				left: 99%;
				top: 50%;
				content: '';
				position: absolute;
				width: 170px;
				height: 165px;
				background: url("../img/text-line.svg") center/contain no-repeat;

				@include media (lg) {
					top: 100%;
					left: 50%;
					transform: translate(-50%, 0);
					width: 2px;
					height: px-to-rem(60, 16);
					background-color: #fff;
				}

				@include media (sm) {
					height: px-to-rem(30, 16);
				}
			}
		}

		&-img {
			background: url("../img/planet-earth-big.webp") center/cover no-repeat;
			position: inherit;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
		}
	}

	&__wrap-form {
		display: flex;
		justify-content: center;
		position: relative;
		z-index: 1;

		.form {
			background: url("../img/stars-background-form-opacity.png") center/cover no-repeat;
		}
	}

}

.no-webp {
	.feedback__bg-img {
		background: url("../img/planet-earth-big.png") center/cover no-repeat;
	}
}
