.mobile-menu {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	visibility: hidden;
	opacity: 0;
	transition: .5s all ease;
	background-color: rgba(255, 255, 255, .98);
	z-index: 20;

	.wrapper {
		position: relative;
		display: flex;
		justify-content: center;
		height: 100%;
		flex-direction: column;
	}

	&_active {
		visibility: visible;
		opacity: 1;
	}

	&__list {
		@include media (md) {
			padding: 0 25px;
		}
	}

	&__item {
		margin-bottom: 1.3rem;

		&:last-child {
			margin-bottom: 0;
		}
	}

	&__link {
		display: block;
		color: $dodger-blue;
		font-size: px-to-rem(26, 16);
		line-height: px-to-rem(36, 16);
	}

}