html.edge {
	.form__field::-webkit-input-placeholder {
		color: rgba(255, 255, 255, .45);
	}
}

html.chrome {
	.services__item-text {
		@include C-Light;
	}
}

html.ie {
	height: 100%;

	body {
		height: 100%;
		opacity: 1;
		transition: 0s ease all;
	}

	.screen-ie {
		display: block;
	}

}

