.banner {
	padding-bottom: px-to-rem(60, 18);
	
	&__mars {
		width: 100%;
		height: auto;
	}

	&__wrap-mars {
		position: absolute;
		right: 0;
		top: -235px;
		width: px-to-rem(1150, 18);
		height: auto;
		transform: translate(52%, 0);
		z-index: -1;

		@include media (xl) {
			width: px-to-rem(850, 16);
			top: 55px;
		}

		@include media (lg) {
			width: px-to-rem(600, 16);
			right: 50%;
			transform: translate(50%, 0);
			top: 250px;
		}

		@include media (sm) {
			top: 180px;
			width: 125vw;
		}
	}

	&__main-content {
		display: flex;
		justify-content: space-between;
		align-items: center;
		position: relative;

		@include media (xl) {
			display: block;
		}
	}

	&__wrap-text {
		width: 100%;
		max-width: 60%;
		margin: 30px 0;
		position: relative;
		top: 23px;

		@include media (xl) {
			max-width: 80%;
			margin: px-to-rem(70, 16) auto;
			text-align: center;
			top: 0;
		}

		@include media (md) {
			max-width: none;
			margin: px-to-rem(70, 14) auto px-to-rem(50, 14);
		}

		@include media (sm) {
			margin: px-to-rem(50, 16) auto px-to-rem(90, 16);
		}
	}

	&__title {
		font-size: px-to-rem(56, 18);
		line-height: px-to-rem(58, 18);
		margin-bottom: px-to-rem(35, 18);
		@include C-ExtraBold;

		@include media (xxl) {
			font-size: px-to-rem(48, 16);
			line-height: px-to-rem(54, 16);
			margin-bottom: px-to-rem(30, 16);
		}

		@include media (sm) {
			font-size: px-to-rem(28, 16);
			line-height: px-to-rem(34, 16);
			margin-bottom: px-to-rem(12, 16);
		}
	}

	&__subtext {
		font-size: px-to-rem(28, 18);
		line-height: px-to-rem(46, 18);

		mark {
			@include C-Bold;
		}

		@include media (xxl) {
			font-size: px-to-rem(26, 16);
			line-height: px-to-rem(42, 16);
		}

		@include media (sm) {
			font-size: 1rem;
			line-height: px-to-rem(24, 16);
		}
	}

	&__wrap-form {
		@include media (xl) {
			display: flex;
			justify-content: center;
		}
	}

	&__bottom-link {
		position: relative;
		line-height: 18px;
		display: inline-block;
		transition: .4s all ease;
		@include C-Light;
		transform: translate(0, -100%);

		@include media (xl) {
			display: none;
		}
		
		&:after {
			content: '';
			position: absolute;
			transition: .4s all ease;
		}

		&:after {
			top: 60%;
			right: -20px;
			transform: translate(0, -50%);
			width: 8px;
			height: 5px;
			background: url("../img/icon-arrow-down-white.svg") center/contain no-repeat;
		}

		&:hover {
			opacity: .8;

			&:before {
				background-color: #fff;
			}

			&:after {
				opacity: inherit;
			}
		}
	}

}

