.form {
	border-radius: 30px;
	padding: px-to-rem(80, 18) px-to-rem(50, 18) px-to-rem(45, 18);
	width: 100%;
	max-width: 490px;
	background: url("../img/stars-background-form.png") center/cover no-repeat;
	text-align: center;
	box-shadow: 0px 25px 19px rgba(0, 0, 0, 0.68);
	overflow: hidden;
	position: relative;

	&:before {
		content: '';
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		background: url("../img/logo.svg") center/contain no-repeat;
		width: 180px;
		height: 180px;
		opacity: 0;
		transition: .4s all ease;
		visibility: hidden;
	}

	&__message {
		opacity: 0;
		visibility: hidden;
		transition: .4s all ease;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);

		&_error {
			color: $red;
		}
	}

	&_loading, &_success, &_error {
		.form__wrap-fields {
			opacity: 0;
			visibility: hidden;
		}
	}

	&_loading {
		&:before {
			visibility: visible;
			opacity: 1;
		}
	}

	&_success .form__message_success,
	&_error .form__message_error {
		opacity: 1;
		visibility: visible;
	}

	@include media (xxl) {
		padding: px-to-rem(70, 18) px-to-rem(40, 18) px-to-rem(35, 18);
	}

	@include media (xl) {
		max-width: 440px;
	}

	@include media (md) {
		max-width: 400px;
	}

	@include media (sm) {
		max-width: 380px;
		padding: px-to-rem(55, 16) px-to-rem(20, 16) px-to-rem(34, 16);
	}

	&__title,
	&__subtitle {
		display: block;
	}

	&__title {
		font-size: px-to-rem(36, 18);
		line-height: px-to-rem(42, 18);
		@include C-Bold;

		@include media (xxl) {
			font-size: px-to-rem(34, 18);
			line-height: px-to-rem(40, 18);
		}

		@include media (sm) {
			font-size: px-to-rem(26, 16);
			line-height: px-to-rem(32, 16);
		}
	}

	&__subtitle {
		color: $accent;
	}

	&__wrap-fields {
		margin: px-to-rem(60, 18) 0 px-to-rem(36, 18);
		padding-bottom: px-to-rem(36, 18);
		border-bottom: 1px solid rgba(245, 245, 245, 0.18);
		transition: .4s all ease;

		@include media (xxl) {
			margin: px-to-rem(50, 18) 0 px-to-rem(36, 18);
		}

		@include media (sm) {
			margin: px-to-rem(20, 16) 0 px-to-rem(25, 16);
			padding-bottom: px-to-rem(25, 18);
		}
	}

	&__wrap-field {
		margin-bottom: 22px;
		display: block;

		@include media (sm) {
			margin-bottom: 20px;
		}

		&:last-of-type {
			margin-bottom: 0;
		}
	}

	&__field {
		border-radius: 30px;
		background-color: rgba(255, 255, 255, 0.25);
		height: 55px;
		width: 100%;
		padding: 8px px-to-rem(30, 18);
		display: block;
		@include C-Regular;
		transition: .4s all ease;
		color: #fff;
		border: 2px solid transparent;

		&_error {
			border-color: rgba($red, .9);
		}

		@include media (sm) {
			height: px-to-rem(50, 16);
			padding: 2px 20px;
		}

		&::placeholder {
			color: rgba(255, 255, 255, .4);
			font-family: inherit;
		}
	}

	&__btn {
		max-width: none;
		line-height: px-to-em(70, 18);
	}

}
