.services {
	padding: px-to-rem(85, 18) 0 0 0;
	z-index: 2;
	position: relative;

	&__inner {
		border-bottom: 1px solid rgba($accent, .3);
		position: relative;
	}

	canvas {
		position: absolute;
		top: 50%;
		right: 0;
		transform: translate(54%, -47%);
		width: 92rem !important;
		height: auto !important;
		z-index: -1;
		pointer-events: none;

		@include media (xl) {
			top: 49.8%;
			width: 80rem !important;
		}

		@include media (lg) {
			display: none;
		}
	}

	&__mars {
		@include media (lg) {
			opacity: 1;
		}

		&-glow {
			position: absolute;
			right: 0;
			top: 10.5%;
			width: 1030px;
			height: auto;
			transform: translate(56.5%, 0);

			@include media (xl) {
				top: 165px;
				width: 50rem;
			}

			@include media (lg) {
				display: none;
			}
		}

		&-img {
			position: absolute;
			right: 0;
			top: 13%;
			transform: translate(28%, 0);
			width: 30rem;
			display: none;

			@include media (lg) {
				display: block;
			}

			@include media (md) {
				display: none;
			}
		}
	}

	&__decor-img {
		position: absolute;
		width: 100%;

		@include media (md) {
			display: none;
		}

		&_left {
			top: 205px;
			max-width: px-to-rem(130, 18);
			right: calc(100% + 80px);
		}
	}

	&__title {
		margin-bottom: px-to-rem(140, 18);

		@include media (xxl) {
			margin-bottom: px-to-rem(110, 16);
		}

		@include media (lg) {
			margin-bottom: px-to-rem(90, 16);
		}

		@include media (md) {
			margin-bottom: px-to-rem(60, 14);
		}
	}

	&__list {

	}

	&__item {
		margin-bottom: px-to-rem(110, 18);
		width: 60%;

		@include media (lg) {
			margin-bottom: px-to-rem(70, 14);

			br {
				display: none;
			}
		}

		@include media (md) {
			margin-bottom: px-to-rem(50, 14);
		}

		@include media (sm) {
			margin-bottom: px-to-rem(50, 16);
		}

		&:last-child {
			margin-bottom: 0;
		}

		&:first-child,
		&:last-child {
			margin-left: px-to-rem(120, 18);

			@include media (xxl) {
				margin-left: px-to-rem(60, 16);
			}

			@include media (lg) {
				margin-left: 0;
				width: 78%;
			}

			@include media (md) {
				width: 100%;
			}
		}

		&:nth-child(2),
		&:nth-child(3) {
			margin-left: px-to-rem(-40, 18);

			@include media (xxl) {
				margin-left: 0;
			}

			@include media (lg) {
				width: 70%;
			}

			@include media (md) {
				width: 100%;
			}
		}
		
		&-title {
			display: block;
			@include C-Bold;
			font-size: px-to-rem(36, 18);
			line-height: px-to-rem(42, 18);
			margin-bottom: px-to-rem(10, 18);
			padding-left: 25px;
			position: relative;

			@include media (sm) {
				font-size: px-to-rem(24, 16);
				line-height: px-to-rem(30, 16);
				margin-bottom: px-to-rem(5, 16);
				padding-left: 20px;
			}

			&:before {
				content: '';
				position: absolute;
				top: 14px;
				left: 0;
				width: 10px;
				height: 10px;
				border-radius: 50%;
				background-color: $accent;

				@include media (lg) {
					top: 13px;
				}

				@include media (md) {
					top: 9px;
				}

				@include media (sm) {
					top: 11px;
					width: 8px;
					height: 8px;
				}
			}
		}

		&-text {
			font-size: px-to-rem(20, 18);
			line-height: px-to-rem(30, 18);
			opacity: .65;

			@include media (sm) {
				font-size: px-to-rem(18, 16);
				line-height: px-to-rem(28, 16);
			}

			em {
				position: relative;

				&:before {
					content: '';
					position: absolute;
					top: 82%;
					left: 50%;
					transform: translate(-50%, 0);
					height: 1px;
					width: 100%;
					background-color: #fff;
				}
			}
		}
	}

	&__btn {
		margin: px-to-rem(140, 18) auto px-to-rem(-27.5, 18);

		@include media (lg) {
			margin: px-to-rem(100, 16) auto px-to-rem(-30.5, 18);
		}

		@include media (sm) {
			margin: px-to-rem(80, 16) auto px-to-rem(-27.5, 16);
		}
	}

}
