.results {
	padding: px-to-rem(95, 18) 0 px-to-rem(100, 18);

	@include media (lg) {
		padding-bottom: px-to-rem(60, 16);
	}

	@include media (sm) {
		padding: px-to-rem(30, 16) 0 px-to-rem(50, 16);
	}

	&__title-group {
		margin-bottom: px-to-rem(100, 18);

		.br-mobile {
			display: none;
		}

		@include media (lg) {
			margin-bottom: px-to-rem(120, 16);
		}

		@include media (sm) {
			margin-bottom: px-to-rem(90, 16);

			.br-mobile {
				display: inline-block;
			}
		}
	}

	&__wrap-slider {
		position: relative;
		padding: 0;

		@media (min-width: 1170px) {
			width: 1195px;
		}
	}

	&__slider {
		.slick-slide {
			padding: 0 15px;
		}

		&-arrows {
			display: flex;
			align-items: center;
			justify-content: space-between;
			margin-top: px-to-rem(65, 18);
			padding: 0 15px;

			@include media (lg) {
				margin: 0;
			}
		}

		&-arrow {
			display: flex;
			align-items: center;
			cursor: pointer;
			transition: .4s all ease;

			@include media (lg) {
				width: 22px;
				position: absolute;
				bottom: calc(100% + 25px);
			}

			@include media (md) {
				width: 18px;
			}

			&.slick-disabled {
				pointer-events: none;
				opacity: .5;
			}

			&:hover:not(.slick-disabled) {
				.results__slider-arrow-text {
					color: $btn-hover;
					display: inline-block;

					&:before {
						background-color: $dodger-blue;
					}
				}

				path {
					stroke: $btn-hover;
				}
			}

			@include media (lg) {
				&:hover:not(.slick-disabled) {
					.results__slider-arrow-text {
						display: none;
					}
				}
			}

			&_prev {
				@include media (lg) {
					right: calc(100% - 15px);
					transform: translate(100%, 0);
				}

				.results__slider-arrow-icon {
					margin-right: 10px;

					@include media (lg) {
						margin: 0;
					}
				}
			}

			&_next {
				@include media (lg) {
					left: calc(100% - 15px);
					transform: translate(-100%, 0);
				}

				.results__slider-arrow-icon {
					margin-left: 10px;

					@include media (lg) {
						margin: 0;
					}
				}
			}

			&-icon {
				width: 5px;
				height: auto;

				@include media (lg) {
					width: 22px;
				}

				@include media (md) {
					width: 18px;
				}
			}

			path,
			&-text {
				transition: .4s all ease;
			}

			&-text {
				color: $accent;
				@include C-Bold;
				font-size: 18px;

				@include media (lg) {
					display: none;
				}
			}
		}

		&-dots {
			display: none;
			margin-top: px-to-rem(60, 16);

			@include media (lg) {
				display: block;
			}

			@include media (sm) {
				margin-top: px-to-rem(50, 16);
			}

			.slick-dots {
				display: flex;
				align-items: center;
				justify-content: center;

				li {
					margin: 0 px-to-rem(15, 16);

					@include media (sm) {
						margin: 0 8px;
					}
				}

				button {
					display: block;
					width: px-to-rem(20, 16);
					height: px-to-rem(20, 16);
					border-radius: 50%;
					transition: .4s all ease;
					background-color: rgba(#fff, .9);
					font-size: 0;

					@include media (sm) {
						width: 12px;
						height: 12px;
					}
				}

				.slick-active {
					button {
						background-color: $accent
					}
				}
			}
			
		}

		&-item {
			position: relative;
			
			&:after {
				content: '';
				width: 100%;
				display: block;
				clear: both;
			}

			&-left,
			&-right {
				width: 50%;
				float: left;

				@include media (lg) {
					float: none;
					width: 100%;
					padding: 0;
				}
			}

			&-left {
				padding-right: px-to-rem(30, 18);

				@include media (lg) {
					margin-bottom: px-to-rem(40, 16);
				}
			}

			&-right {
				padding-left: px-to-rem(84, 18);
			}

			em,
			p,
			&-subtitle,
			&-title,
			&-case-link {
				display: inline-block;
			}

			em,
			p,
			&-subtitle,
			&-title {
				@include media (lg) {
					display: block;
				}
			}

			em {
				@include C-Bold;
				margin-bottom: px-to-rem(12, 18);
			}

			p {
				@include C-ExtraLight;
				opacity: .8;
			}

			&-about {
				margin-bottom: px-to-rem(65, 18);

				@include media (md) {
					margin-bottom: px-to-rem(30, 14);
				}
			}

			&-subtitle {
				color: rgba(255, 255, 255, .43);

				@include media (sm) {
					font-size: px-to-rem(16, 16);
					line-height: px-to-rem(22, 16);
				}
			}

			&-title {
				font-size: px-to-rem(36, 18);
				line-height: px-to-rem(42, 18);
				@include C-Bold;

				@include media (sm) {
					font-size: px-to-rem(24, 16);
					line-height: px-to-rem(30, 16);
					margin: 8px 0;
				}
			}

			&-case-link {
				color: $link;
				text-decoration: none;
			
				&:before {
					display: block;
				}

				@include media (lg) {
					text-decoration: none;

					&:before {
						display: block;
					}
				}

				&:hover {
					color: $accent;

					&:before {
						background-color: $accent;
					}
				}
			}

			&-diagram-title {
				margin-top: px-to-rem(40, 18);
			}

			&-diagrams {
				margin-left: -32px;
				position: relative;

				&:after {
					content: '';
					width: 100%;
					display: block;
					clear: both;
				}

				@include media (md) {
					margin-left: -20px
				}
			}

			&-diagram {
				max-width: 170px;
				width: 100%;
				margin-bottom: px-to-rem(30, 18);
				display: flex;
				flex-direction: column;
				align-items: center;
				float: left;

				@include media (xl) {
					max-width: 130px;
				}

				@include media (sm) {
					max-width: 115px;
				}

				&-info {
					@include C-Bold;
					order: 2;
					margin-top: -7px;
				}

				svg {
					order: 1;
					width: 100%;
					height: auto;
				}

				.border {
					display: none;
				}

				circle{
					transition: 1.8s all ease;
					
					&:last-of-type {
						stroke: rgba(255, 255, 255, .2);
					}
				}

				.timer {
					@include C-Bold;
					fill: #fff;
					transform: translate(0, 9px);
				}
			}

			&-percent {
				display: flex;
				align-items: flex-end;
				padding-top: px-to-rem(52, 18);
				margin-bottom: px-to-rem(50, 18);

				@include media (lg) {
					padding-top: px-to-rem(25, 16);
					margin-bottom: px-to-rem(25, 16);
				}

				@include media (sm) {
					flex-direction: column;
					align-items: flex-start;
					margin-bottom: px-to-rem(35, 16);
					padding: 0;
				}

				&-text {
					margin: 0 px-to-rem(35, 18) 10px 0;

					&:hover {
						&:before {
							background-color: #fff;
						}
					}

					@include media (lg) {
						text-decoration: none;

						&:before {
							display: block;
						}
					}

					@include media (sm) {
						margin: 0;
						order: 2;
					}
				}

				&-number {
					font-size: px-to-rem(64, 18);
					line-height: px-to-rem(64, 18);
					color: $green;
					@include C-Bold;

					@include media (sm) {
						font-size: px-to-rem(42, 16);
						line-height: px-to-rem(50, 16);
						margin-bottom: 5px;
						order: 1;
					}
				}
			}

			&-search-systems {
				@include media (sm) {
					margin-top: px-to-rem(20, 16);
				}
			}

			&-search-system {
				margin-bottom: px-to-rem(45, 18);

				&:last-child {
					margin-bottom: 0;
				}

				&-title {
					display: flex;
					align-items: flex-end;
					margin-bottom: 12px;
					line-height: 22px;
					white-space: nowrap;
					@include C-ExtraLight;
					opacity: .8;

					br {
						display: none;
					}

					@include media (xl) {
						display: block;
						width: 100%;

						br {
							display: inline-block;
						}

						img {
							display: inline-block;
							position: relative;
							top: 5px;
						}
					}

					@include media (lg) {
						br {
							display: none;
						}

						img {
							top: 3px;
						}
					}

					@include media (sm) {
						br {
							display: inline-block;
						}

						img {
							top: 5px;
						}
					}

					&-img {
						max-width: 75px;
						height: 26px;
						margin-left: 10px;
						position: relative;
						bottom: 2px;
					}
				}

				&-wrap-img {
					@include stretchHeight(43.5%);
					position: relative;

					@include media (lg) {
						padding: 0;
					}
				}

				&-img {
					width: 100%;
					height: auto;
					position: absolute;
					top: 0;
					left: 0;

					@include media (lg) {
						position: static;
					}
				}
			}

		}
	}

}

