* {
	box-sizing: border-box;
}

html{
	font-size: 18px;
	overflow-x: hidden;

	&.page-fixed {
		overflow: hidden;
	}

	@include media (xl) {
		font-size: 16px;
	}

	@include media (md) {
		font-size: 14px;
	}

	@include media (sm) {
		font-size: 16px;
	}

	@include media (xs) {
		font-size: 13px;
	}
}

body {
	min-width: 320px;
	position: relative;
	line-height: 30px;
	color: $default-text;
	font-kerning: normal;
	@include C-Regular;
	background-color: #000;
	transition: .6s opacity ease;
	overflow-x: hidden;

	> * {
		z-index: 1;
		position: relative;
	}

	&.page-fixed {
		overflow-y: scroll;
	}

	&.visible{
		opacity: 1;
	}

	@include media (xl) {
		line-height: 26px;
	}

	@include media (md) {
		line-height: 22px;
	}

	@include media (sm) {
		line-height: 28px;
	}

	@include media (xs) {
		line-height: 22px;
	}
}


.page-bg {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	background-size: cover;
	background-image: url("../img/stars-background-page.jpg");
	background-position: center;
	background-repeat: no-repeat;
	z-index: -1;
}

.btn-popup {
	cursor: pointer;
}

.disabled {
	pointer-events: none;
}

img.lazy {
	transition: .6s all ease;
	opacity: 0;

	&.loaded,
	&.error {
		opacity: 1;
	}
}

a {
	transition: .3s color ease;
	color: #fff;
}

.text-underline {
	position: relative;
	line-height: 20px;

	@include media (lg) {
		text-decoration: underline;

		&:before {
			display: none;
		}
	}

	&:before {
		content: '';
		position: absolute;
		transition: .4s all ease;
		top: 100%;
		left: 50%;
		transform: translate(-50%, 0);
		height: 1px;
		width: 100%;
		background-color: #fff;
	}

	&:hover {
		&:before {
			background-color: $accent;
		}
	}

	&_blue {
		&:before {
			background-color: $dodger-blue;
		}

		&:hover {
			&:before {
				background-color: $accent;
			}
		}
	}

	&_orange {
		&:before {
			background-color: $accent;
		}

		&:hover {
			&:before {
				background-color: $dodger-blue;
			}
		}
	}
}

input {
  font-size: 18px;

  @include media (xs) {
    font-size: 14px;
  }
}

mark {
	color: $accent;
}

::selection {
	background-color: $accent;
	color: #fff;
}

.block-title,
.block-subtitle {
	text-align: center;
	display: block;
	margin-left: auto;
	margin-right: auto;
}

.block-title {
	@include C-Bold;
	font-size: px-to-rem(48, 18);
	line-height: px-to-rem(58, 18);
	margin-bottom: 1rem;

	@include media (md) {
		font-size: px-to-rem(36, 14);
		line-height: px-to-rem(42, 14);
	}

	@include media (sm) {
		font-size: px-to-rem(28, 16);
		line-height: px-to-rem(34, 16);
	}
}

.block-subtitle {
	font-size: px-to-rem(24, 18);
	line-height: px-to-rem(32, 18);

	@include media (md) {
		font-size: px-to-rem(20, 14);
		line-height: px-to-rem(36, 14);
	}

	@include media (sm) {
		font-size: px-to-rem(18, 16);
		line-height: px-to-rem(26, 16);
	}
}
