.popup {
	max-height: 100vh;
	overflow-y: auto;
	transition: .6s all ease;
	font-size: 16px;
	z-index: 20;
	display: flex;
	align-items: center;
	justify-content: center;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	opacity: 0;
	visibility: hidden;
	background-color: rgba(#000, .8);

	&_visible {
		opacity: 1;
		visibility: visible;

		.popup__content {
			transform: translate(0);
			opacity: 1;
		}
	}

	&__wrap-content {
		height: 100vh;
		width: 100%;
		overflow-y: scroll;

		@media (max-width: 576px) and (orientation: portrait) {
			display: flex;
			align-items: center;
			justify-content: center;
		}
	}

	&__content {
		margin: 10vh auto;
		width: calc(100% - 30px);
		max-width: 500px;
		position: relative;
		transition: .8s all ease;
		transform: translate(0, 40px);
		opacity: 0;

		@include media (md) {
			max-width: 400px;
			margin: 6vh auto;
		}

		.form {
			max-width: none;
			box-shadow: 0px 30px 30px rgba($accent, 0.1);
			background-color: #EDEFF1;
		}
	}
	 
	&__close {
		position: absolute;
		top: 20px;
		right: 25px;
		width: 20px;
		height: 20px;
		cursor: pointer;
		z-index: 2;

		&:before, &:after {
			content: '';
			position: absolute;
			top: 40%;
			left: 0%;
			background-color: $dodger-blue;
			height: 2px;
			width: 25px;
			transition: .4s all ease;
			transform-origin: 50% 50%;
		}

		&:hover {
			&:before, &:after {
				background-color: $accent;
			}

			&:before {
				transform: rotate(45deg);
			}

			&:after {
				transform: rotate(-45deg);
			}
		}

		&:before {
			transform: rotate(-45deg)
		}

		&:after {
			transform: rotate(45deg)
		}


		@include media (md) {
			width: 20px;
			height: 20px;
			right: 20px;
			top: 20px;
		}
	
		@include media (xs) {
			top: 15px;
			right: 15px;
		}

		&:before, &:after {
			@include media (md) {
				width: 20px;
			}
		}
	}

	&-image {
		.popup {
			&__close {
				top: 0;
				right: 0;
				transform: translate(150%, 0);

				@include media (md) {
					transform: translate(0, -150%);
				}
			}

			&__wrap-content {
				display: flex;
				align-items: center;
				justify-content: center;
			}

			&__content {
				max-width: 80%;

				@include media (md) {
					max-width: 95%;
				}
			}
		}

		&__wrap-img {
			@include stretchHeight(43.5%);
			position: relative;
			background-color: #fff;

			&_onload {
				.popup-image__wrap-img {
					&:before {
						opacity: 0;
					}
				}

				.popup-image__img {
					opacity: 1;
				}
			}

			&:before {
				content: "";
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				width: 250px;
				height: 250px;
				background: url("../img/logo-dark.svg") center/contain no-repeat;
				transition: .3s all ease;

				@include media (md) {
					width: 150px;
					height: 150px;
				}
			}
		}

		&__img {
			max-width: 100%;
			max-height: 100%;
			width: 100%;
			height: auto;
			position: absolute;
			position: absolute;
			top: 0;
			left: 0;
			transition: .3s all ease;
			opacity: 0;
			z-index: 1;
		}
	}

}
