.footer {

	&__top {
		padding: px-to-rem(24, 18) 0 px-to-rem(35, 18);
		border-top: 1px solid rgba(#fff, .32);

		@include media (lg) {
			padding: px-to-rem(20, 16) 0 px-to-rem(28, 16);
		}
	}

	.panel__menu-btn {
		display: none;
	}

	&__middle,
	&__bottom {
		
		@include media (md) {
			text-align: center;
		}

		&-inner {
			display: flex;
			align-items: center;
			justify-content: space-between;
			flex-wrap: wrap;
		}

		a:hover {
			color: $accent;
		}
	}

	&__middle {
		&-inner {
			padding: px-to-rem(30, 18) 0 px-to-rem(15, 18);
			border-bottom: 1px solid rgba(#fff, .1);

			@include media (xxl) {
				flex-direction: column;
				justify-content: center;
				align-items: center;
			}

			@include media (md) {
				padding-bottom: px-to-rem(25, 14);
			}
		}

		&-group {
			display: flex;
			align-items: center;
			flex-wrap: wrap;
			margin-bottom: px-to-rem(15, 18);

			@include media (md) {
				display: block;
				text-align: center;
				margin-bottom: 25px;

				&:last-child {
					margin-bottom: 0;
				}
			}

			&:last-child {
				@include media (xxl) {
					margin-left: auto;
					margin-right: auto;
				}

				@include media (md) {
					width: 100%;

					.footer__middle-group-title {
						display: block;
					}
				}
			}

			&-title {
				color: $accent;
				margin-right: 4px;
				@include C-Bold;

				@include media (md) {
					display: block;
					margin-right: 0;
				}
			}
		}

		address {
			margin-right: 14px;
		}
	}

	&__link {
		&-map {
			@include C-Bold;
			color: $link;
		}

		&_social {
			@include C-ExtraLight;
			margin-right: px-to-rem(25, 18);
			opacity: .6;

			@include media (md) {
				display: inline-block;
			}

			@include media (sm) {
				margin-right: px-to-rem(18, 16);
			}

			&:first-of-type {
				margin-left: px-to-rem(18, 16);
			}

			&:last-child {
				margin-right: 0;
			}
		}
	}

	&__bottom {
		&-inner {
			padding: px-to-rem(30, 18) 0;

			@include media (md) {
				display: block;
				padding-bottom: 40px;
			}
		}

		&-text {
			font-size: px-to-rem(24, 18);
			line-height: px-to-rem(30, 18);
			@include C-Bold;

			br {
				display: none;
			}

			@include media (xl) {
				font-size: px-to-rem(20, 16);
				line-height: px-to-rem(26, 16);
			}

			@include media (md) {
				font-size: px-to-rem(16, 14);
				line-height: px-to-rem(22, 14);
				margin-bottom: 20px;

				br {
					display: inline-block;
				}
			}

			@include media (md) {
				font-size: 1rem;
				line-height: px-to-rem(22, 16);
			}

			&-link {
				color: $link;
				display: inline-block;
			}
		}

		&-copyright {
			font-size: px-to-rem(14, 18);
			@include C-ExtraLight;
		}
	}

	.panel {
		&__logo {
			@include media (sm) {
				margin: 0 auto;
			}
		}

		&__nav {
			@include media (lg) {
				display: none;
			}
		}

		&__phone {
			@include media (sm) {
				display: none;
			}
		}
	}

}