.wrapper,
.wrapper-fluid {
	margin: 0 auto;
	padding: 0 15px;
}

@media (min-width: 576px) {
	.wrapper,
	.wrapper-fluid {
		width: 570px;
	}
}

@media (min-width: 768px) {
	.wrapper,
	.wrapper-fluid {
		width: 750px;
	}
}

@media (min-width: 992px) {
	.wrapper,
	.wrapper-fluid {
		width: 990px;
	}
}

@media (min-width: 1170px) {
	.wrapper,
	.wrapper-fluid {
		width: 1165px;
	}
}

@media (min-width: 1400px) {
	.wrapper-fluid {
		width: 1325px;
	}
}
