.header {
	z-index: 2;
	
	&__inner {
		padding: px-to-rem(40, 18) 0;

		@include media (xl) {
			padding: px-to-rem(30, 16) 0;
		}

		@include media (lg) {
			padding: px-to-rem(22, 16) 0;
		}
	}

	.panel {
		&__phone {
			@include media (lg) {
				display: none;
			}
		}
	}

	&-scroll {
		top: 0;
		left: 0;
		position: fixed;
		width: 100%;
		transform: translate(0, -105%);
		background-color: #fff;
		visibility: hidden;
		transition: .3s transform ease, .3s visibility ease;
		z-index: 10;
		box-shadow: 0px 10px 55px rgba(0, 92, 199, 0.07);

		&_visible {
			transform: translate(0);
			visibility: visible;
		}

		.header {
			&__inner {
				padding: px-to-rem(18, 18) 0;

				@include media (xl) {
					padding: px-to-rem(18, 16) 0;
				}

				@include media (lg) {
					padding: px-to-rem(20, 16) 0;
				}
			}
		}

		.panel {
			&__logo {
				width: 105px;
				margin-right: px-to-rem(165, 18);

				@include media (lg) {
					margin-right: 0;
				}

				@include media (sm) {
					width: 90px;
				}

				&-img {
					path {
						fill: #000;
					}
				}
			}

			&__nav {
				top: 5px;

				&-link {
					color: #000;

					&:hover {
						color: $link;
					}
				}
			}

			&__phone {
				@include media (lg) {
					display: block;
					order: 2;
					margin-left: auto;
					margin-right: auto;
					position: relative;
					top: 3px;

					&-text {
						display: none;
					}
				}

				@include media (sm) {
					&-number {
						font-size: px-to-rem(14, 16);
					}
				}

				&-text,
				&-number {
					color: #000;
				}

				&-number {
					&:hover {
						color: $link;
					}
				}
			}

			&__menu-btn {
				@include media (lg) {
					order: 3;
				}
			}
		}
	}

}
